import React, { Component } from 'react';
import classes from './BurgerIngredient.css';
import PropTypes from 'prop-types';

class BurgerIngredient extends Component {
  render() {
    let ingredient = null;

    switch (this.props.type) {
      case ('bread-bottom'):
        ingredient = <div className={classes.BreadBottom}></div>
        break;
      case ('bread-top'):
        ingredient = (<div className={classes.BreadTop}>
                      <div className={classes.Seeds1}></div>
                      <div className={classes.Seeds2}></div>
                      </div>
                    );
                    break;
      case ('meat'):
        ingredient = <div className={classes.Meat} onClick={this.props.removed}></div>
        break;
      case ('cheese'):
        ingredient = <div className={classes.Cheese} onClick={this.props.removed}></div>
        break;
      case ('bacon'):
        ingredient = <div className={classes.Bacon} onClick={this.props.removed}></div>
        break;
      case ('salad'):
        ingredient = <div className={classes.Salad} onClick={this.props.removed}></div>
        break;
      default:
        ingredient = null;
    }
    return ingredient;
  }


};

BurgerIngredient.propTypes = {
  type: PropTypes.string.isRequired
}

export default BurgerIngredient;



